<template>
  <div class="address">
    <nut-cell  :desc="text" is-link @click="showAddressOther"></nut-cell>
  <nut-address
      v-model:visible="showPopupOther"
      type="exist"
      :exist-address="existAddress"
      :province="province"
      :city="city"
      :country="country"
      :town="town"
      :back-btn-icon="backBtnIcon"
      @close="close"
      @selected="selected"
      custom-and-exist-title=""
      @switch-module="switchModule"
      @close-mask="closeMask"
  ></nut-address>
  </div>
</template>

<script>
  import { ref,reactive,toRefs } from 'vue';
  export default {
    setup() {
        const showPopupOther = ref(false);
        const address = reactive({
          province:[
            { id: 1, name: '' },
            { id: 2, name: '' },
            { id: 3, name: '' },
            { id: 4, name: '' }
          ],
          city:[
            { id: 7, name: '' },
            { id: 8, name: '' },
            { id: 9, name: '' },
            { id: 6, name: '' }
          ],
          country:[
            { id: 3, name: '' },
            { id: 9, name: '' },
            { id: 4, name: '' }
          ],
          town:[]
        })
        const existAddress = ref([
          {
            id: 1,
            addressDetail: '',
            cityName: '',
            countyName: '',
            provinceName: '',
            selectedAddress: true,
            townName: '',
            name: '',
            phone: '182****1718'
          },
          {
            id: 2,
            addressDetail: '',
            cityName: '',
            countyName: '',
            provinceName: '',
            selectedAddress: false,
            townName: '',
            name: '',
            phone: '182****1718'
          },
          {
            id: 3,
            addressDetail: '',
            cityName: '',
            countyName: '18',
            provinceName: '',
            selectedAddress: false,
            townName: '',
            name: '',
            phone: '182****1718'
          }
        ]);
        const backBtnIcon = ref('left')
        const text = ref('')

        const showAddressOther = () => {
          showPopupOther.value = true;
        };

        const close = val => {
          if (val.type == 'exist') {
            const {provinceName,cityName,countyName,townName,addressDetail} = val.data
            text.value = provinceName + cityName + countyName + townName + addressDetail;
          } else {
            text.value = val.data.addressStr;
          }
        };

        // eslint-disable-next-line no-unused-vars
        const selected = (prevExistAdd, nowExistAdd, arr) => {
          console.log(prevExistAdd);
          console.log(nowExistAdd);
        };

        const switchModule = cal => {
          if (cal.type == 'custom') {
            console.log('');
          } else {
            console.log('');
          }
        };

        const closeMask = val => {
          console.log('', val);
        };

        return { showPopupOther, text, existAddress,showAddressOther, switchModule, closeMask, close, selected, backBtnIcon, ...toRefs(address) };
    }
  }
</script>

<style scoped>
.address{
  position: relative;
  flex: 1;
  height: 100%;
  border: 0px red solid;
  text-align: left;
  line-height: 45px;
  margin-left: 30px;
}
.address::after{
  background: #bfbfbf;
  content: "";
  height: 0.6rem;
  margin-top: -0.25rem;
  position: absolute;
  right: 0;
  top: 50%;
  width: 0.8px;
}
.address::before{
  background: url(//jstatic.3.cn/static/icon_location_jd@2x.9ca2376c.png);
  background-size: 12px 12px;
  content: "";
  height: 12px;
  left: -15px;
  margin-top: -7px;
  position: absolute;
  top: 50%;
  width: 12px;
}
.address >>> .nut-cell{
   position: relative;
    display: flex;
    width: 100%;
    line-height: 20px;
    margin: 0px 0;
    box-shadow: 0 0px 0px #edeef1;
    padding: 13px 2px;
    height:100%;
    border: 0px red solid;
    border-radius: 0px;
}
.address >>> .nut-icon-right:before {
    content: "" !important;
}
.address >>> .nut-cell__value{
  flex: 1;
  height: 20px;
  font-size: 13px;
  color: #262626;
  overflow: hidden !important;
  text-overflow:ellipsis !important;
  white-space: nowrap !important;
  -webkit-line-clamp: 1 !important;
  display: -webkit-box !important;
  -webkit-box-orient: vertical !important;
  border: 0px red solid;
  text-align: left !important;
}
</style>